@import url(app.css);

/* Flex utility class */
.flex-center {
  display: flex;
  justify-content: center;
  align-items: center;
}

/* Section styling */
.apply-doctor-section {
  padding: 2rem;
  background-color: #f9f9f9;
}

/* Container styling */
.apply-doctor-container {
  background-color: #fff;
  padding: 2rem;
  border-radius: 8px;
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
  width: 100%;
  max-width: 600px;
  flex-direction: column;
}

/* Form heading */
.form-heading {
  font-size: 1.5rem;
  margin-bottom: 1.5rem;
  text-align: center;
}

/* Form input styling */
.form-input {
  width: 100%;
  padding: 0.75rem;
  margin-bottom: 1rem;
  border: 1px solid #ccc;
  border-radius: 4px;
  font-size: 1rem;
}

/* Button styling */
.btn {
  padding: 0.75rem 1.5rem;
  border: none;
  border-radius: 4px;
  font-size: 1rem;
  cursor: pointer;
}

.form-btn {
  background-color: #007bff;
  color: #fff;
}

.form-btn:disabled {
  background-color: #ccc;
  cursor: not-allowed;
}

/* Availability section */
.availability-section {
  margin-top: 1.5rem;
}

.day-availability {
  margin-bottom: 1rem;
}

.day-header {
  display: flex;
  align-items: center;
  margin-bottom: 0.5rem;
}

.time-inputs {
  display: flex;
  align-items: center;
  gap: 0.5rem;
  padding-left: 20px;
}

.form-checkbox{
  margin-right: 10px;
}